<template>
  <div id="app">
    <div class="help">
      <h1>How to</h1>
      <ul style="text-align:left">
        <li>Space - переключает режим отображения направляющих</li>
        <li>клик ЛКМ по пересечению направляющих - переход в режим рисования линии (от этой точки)</li>
        <li>клик ЛКМ по пересечению направляющих в режиме рисования линий - нарисовать линию между двумя точками (до этой точки)</li>
        <li>Ctrl+клик ЛКМ по пересечению направляющих - нарисовать горизонтальную линию через пересечение направляющих</li>
        <li>Shift+клик ЛКМ по пересечению направляющих - нарисовать вертикальную линию через пересечение направляющих</li>
        <li>двойной клик ЛКМ по направляющей (не самый простой манёвр) - нарисовать линию, совпадающую с этой направляющей</li>
      </ul>
    </div>
    <line-drawer :canvas-height=canvasHeight :canvas-width=canvasWidth :meters-height=metersHeight :meters-width=metersWidth :cell-meters-size=cellSize :space-state="state" />
    <div class="help">
      <h1>Текущие параметры</h1>
      <ul style="text-align:left">
        <li>Размер области для отисовки: {{canvasWidth}} x {{canvasHeight}} px</li>
        <li>Размер области под плитку: {{metersWidth}} x {{metersHeight}} м</li>
        <li>Размер ячейки сетки: {{cellSize}} м</li>
      </ul>
    </div>
  </div>
</template>

<script>
import LineDrawer from "@/components/LineDrawer";

export default {
  name: 'App',
  components: {
    LineDrawer
  },
  data() {
    return {
      state: 1,
      canvasHeight: 400,
      canvasWidth: 600,
      metersWidth: 3.1,
      metersHeight: 2,
      cellSize: 0.15,
    };
  },
  methods: {
    changeState() {
      let newState = this.state;
      newState++;
      this.state = newState <= 2 ? newState : 0;
    }
  },
  mounted() {
    var me = this;
    document.onkeypress = (evt) => {
      const charCode = evt.code || evt.button;
      if (charCode === 'Space') {
        me.changeState();
      }
    };
  }
}
</script>

