<template>
  <div id="container"></div>
</template>

<script>

import Konva from 'konva';
import helpers from "@/components/helpers";

export default {
  name: 'LineDrawer',
  props: {
    msg: String,
    canvasWidth: Number,
    canvasHeight: Number,
    metersWidth: Number,
    metersHeight: Number,
    cellMetersSize: Number,
    spaceState: {
      type: Number,
      default: 1,
      validator: value => value >=0 && value <= 2,
    }
  },
  watch: {
    spaceState(value) {
      return this._changeRulersSate(value);
    },
  },
  data() {
    return {
    };
  },
  grid: null,
  gridOpacity: 0.4,
  dotField: null,
  computed: {
  },
  methods: {
    _changeRulersSate(state) {
      switch(state) {
        case 0:
          this.$options.grid.opacity(0);
          this.$options.dotField.opacity(0);
          break;
        case 1:
          this.$options.grid.opacity(this.$options.gridOpacity);
          this.$options.dotField.opacity(0);
          break;
        case 2:
          this.$options.grid.opacity(this.$options.gridOpacity);
          this.$options.dotField.opacity(1);
          break;
      }
    },
    _getPixelToMeter(padding) {
      const proportional = this.canvasWidth / this.canvasHeight;
      const metersProportional = this.metersWidth / this.metersHeight;
      if (proportional >= metersProportional) {
        return Math.round(this.canvasHeight - 2 * padding) / this.metersHeight;
      }
      return Math.round(this.canvasWidth - 2 * padding) / this.metersWidth;
    },
    _activateDotLayer() {
      this.$options.dotField.hide();
      this.$options.selectedDotField.show();
    },
    _deactivateDotLayer() {
      this.$options.dotField.show();
      this.$options.selectedDotField.hide();
    },
    _buildStrokeLayer() {
      const stroke = new Konva.Layer()
      stroke.add(new Konva.Rect({
        width: this.canvasWidth,
        height: this.canvasHeight,
        stroke: 'black',
        fill: '#1b53a7',
        strokeWidth: 1,
      }));
      return stroke;
    },
  },
  mounted() {
    const padding = 10;

    const pixelToMeter = this._getPixelToMeter(padding);
    const workspaceWidth = this.metersWidth * pixelToMeter
    const workspaceHeight = this.metersHeight * pixelToMeter
    const cellSize = this.cellMetersSize * pixelToMeter;

    const leftPadding = Math.floor((this.canvasWidth - 2 * padding - workspaceWidth)/2) + padding;
    const topPadding = Math.floor((this.canvasHeight - 2 * padding - workspaceHeight)/2) + padding;

    this.$options.grid = new Konva.Layer({
      opacity: this.$options.gridOpacity,
      x: leftPadding,
      y: topPadding,
    });
    const dotField = this.$options.dotField = new Konva.Layer({
      opacity: 0,
      x: leftPadding,
      y: topPadding,
    });
    const selectedDotField = this.$options.selectedDotField = new Konva.Layer({
      x: leftPadding,
      y: topPadding,
      clearBeforeDraw: true,
      visible: false,
    });
    const drawnLines = this.$options.drawnLines = new Konva.Layer({
      x: leftPadding,
      y: topPadding,
    });

    const cellNumberX = Math.floor(this.metersWidth/this.cellMetersSize);
    const cellNumberY = Math.floor(this.metersHeight/this.cellMetersSize);

    const drawLineRaw = (x1, y1, x2, y2) => {
      drawnLines.add(helpers.getLine(x1, y1, x2, y2));
    }

    const drawLine = (first, second) => {
      drawLineRaw(first.x(), first.y(), second.x(), second.y());
      selectedDotField.hide();
      dotField.show();
    }

    const drawRulerLine = (x1, y1, x2, y2) => {
      this.$options.grid.add(helpers.getRulerLine(x1, y1, x2, y2, (event) => {
        if (event.evt.ctrlKey || event.evt.shiftKey) {
          drawLineRaw(...event.target.attrs.points);
        }
      }));
    }

    const drawCircle = (xCoord, yCoord) => {
      const vm = this;
      dotField.add(helpers.getCircle(xCoord, yCoord, function(event) {
        let firstTarget = event.target;
        if(event.evt.ctrlKey) {
          drawLineRaw(0, firstTarget.y(), workspaceWidth, firstTarget.y());
        }
        if (event.evt.shiftKey) {
          drawLineRaw(firstTarget.x(), 0, firstTarget.x(), workspaceHeight);
        }
        if (!event.evt.ctrlKey && !event.evt.shiftKey) {
          selectedDotField.destroyChildren();
          for (let i = 0; i <= cellNumberY; i++) {
            selectedDotField.add(helpers.getCircle(event.target.x(), i * cellSize, event => drawLine(firstTarget, event.target)));
          }
          selectedDotField.add(helpers.getCircle(event.target.x(), workspaceHeight, event => drawLine(firstTarget, event.target)));
          for (let i = 0; i <= cellNumberX; i++) {
            selectedDotField.add(helpers.getCircle(i * cellSize, event.target.y(), event => drawLine(firstTarget, event.target)));
          }
          selectedDotField.add(helpers.getCircle(workspaceWidth, event.target.y(), event => drawLine(firstTarget, event.target)));
          selectedDotField.add(helpers.getCircle(event.target.x(), event.target.y(), () => vm._deactivateDotLayer()));
          vm._activateDotLayer()
        }
      }));
    };

    const drawCircles = (xCoord) => {
      for (let j = 0; j <= cellNumberY; j++) {
        drawCircle(xCoord, j* cellSize);
      }
      drawCircle(xCoord, workspaceHeight);
    };

    for (let i = 0; i <= cellNumberX; i++) {
      drawRulerLine(i*cellSize, workspaceHeight, i*cellSize, 0);
      drawCircles(i * cellSize);
    }
    drawRulerLine(workspaceWidth, 0, workspaceWidth, workspaceHeight);
    drawCircles(workspaceWidth);
    for (let i = 0; i <= cellNumberY; i++) {
      drawRulerLine(workspaceWidth, i*cellSize, 0, i*cellSize);
    }
    drawRulerLine(0, workspaceHeight, workspaceWidth, workspaceHeight);

    const stage = new Konva.Stage({
      container: 'container',
      width: this.canvasWidth,
      height: this.canvasHeight
    });

    stage.add(this._buildStrokeLayer())
    stage.add(this.$options.grid)
    stage.add(drawnLines)
    stage.add(dotField)
    stage.add(selectedDotField)
  }
}
</script>

