import Konva from "konva";

export default {
    getCircle(x, y, click) {
        let circle = new Konva.Circle({
            x: x,
            y: y,
            radius: 5,
            fill: '#509aef',
            stroke: '#b9eff8',
            strokeWidth: 1
        });
        if (click) {
            circle.on('click', click);
        }
        return circle;
    },
    getLine(x1, y1, x2, y2) {
        return this._getLine(x1, y1, x2, y2, '#b9eff8', 3);
    },
    getRulerLine(x1, y1, x2, y2, click) {
        let line = this._getLine(x1, y1, x2, y2, '#509aef');
        if (click) {
            line.on('click', click);
        }
        return line;
    },
    _getLine(x1, y1, x2, y2, strokeColor, strokeWidth = 1) {
        return new Konva.Line({
            points: [x1, y1, x2, y2],
            stroke: strokeColor,
            strokeWidth: strokeWidth
        });
    }
}
